import '@/script/js/anime.min'
// import '@/script/js/common'
import '@/script/js/easing'
import '@/script/js/fittext'
// @ts-ignore
import gsap from '@/script/js/gsap.min'
import '@/script/js/lettering'
import LocomotiveScroll from '@/script/js/locomotive-scroll'
import '@/script/js/nice-select'
// @ts-ignore
import ScrollTrigger from '@/script/js/ScrollTrigger.min'
import MetaSetting from "@/script/metaSetting";

@Component
export default class ZemJam extends Vue {
    scroller: any = null;
    metaSetting !: MetaSetting;

    mounted() {

        this.metaSetting = new MetaSetting({
            title: `ZEM JAM | Zempie.com`, // 
            meta: [
                {
                    name: 'description',
                    content: `Make your awesome clicker game within 10 days and get a chance to win great prizes all skill levels participants are welcomed to join`
                },
                {property: 'og:url', content: `${this.$store.getters.homeUrl}/${this.$i18n.locale}/zem-jam`},
                {property: 'og:title', content: `ZEM JAM  | Zempie.com`},
                {
                    property: 'og:description',
                    content: `Make your awesome clicker game within 10 days and get a chance to win great prizes all skill levels participants are welcomed to join`
                },
                {property: 'og:image', content: '/img/zemjam_logo.png'},
                {property: 'og:type', content: 'website'},

            ]
        });

        this.$nextTick(() => {
            this.initLocoScroll();
        })
    }

    initLocoScroll() {

        //@ts-ignore
        this.scroller = new LocomotiveScroll({
            el: (this.$refs['eventRef'] as HTMLElement),
            smooth: true,
            smoothMobile: true,
            mobile: {
                getDirection: true,
                smooth: true
            },
            tablet: {
                getDirection: true,
                smooth: true
            },
            lerp: 0.05,
            smartphone: {
                smooth: false,
                // direction: "vertical"
            },
        });


        this.scroller.update()
    }

    neverOpen() {
        localStorage.setItem('zj-popup-open', 'false')
        this.$modal.hide('openEventPopup')
    }

    openApplyForm() {
        if (this.$i18n.locale === 'ko') {
            window.open('https://forms.gle/TmuUzjtK6o8bahqA7', "_blank")
        }
        else {
            window.open('https://forms.gle/X2ZhXzUE7aV41xRT9', "_blank")
        }
    }

    beforeDestroy() {
        this.scroller.destroy()
    }


}
